import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import Image from 'gatsby-image'


import { rhythm } from '../utils/typography'
import styled from 'styled-components';
import pointRight from '../../static/icon/hand-pointing-right.png'
import wechatQR from '../../content/assets/wechat.png'
import { withStyles } from '@material-ui/core/styles';
import Popper from '@material-ui/core/Popper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';

class Bio extends React.Component {

  state = {
    anchorEl: null,
    open: false,
  }

  handleClick = event => {
    const { currentTarget } = event;
    this.setState(state => ({
      anchorEl: currentTarget,
      open: !state.open
    }))
  }

  render() {
    const { anchorEl, open } = this.state;
    const id = open ? 'simple-popper' : null;
    return (
      <StaticQuery
        query={bioQuery}
        render={data => {
          const { author, social } = data.site.siteMetadata
          return (
            <BioCard>
              <Image
                fixed={data.avatar.childImageSharp.fixed}
                alt={author}
                style={{
                  minWidth: 50,
                  borderRadius: `50%`,
                  margin: "1rem 0"
                }}
                imgStyle={{
                  borderRadius: `50%`,
                }}
              />
              <BriefIntro>
                <Ul>
                  <Li>学生物不得的伪生物狗</Li>
                  <Li>代码真香</Li>
                  <Li>自带debuff</Li>
                </Ul>
              </BriefIntro>
  
              <ContactInfo>
                <Button onClick={this.handleClick}>
                  <Image
                    fixed={data.wechatQR.childImageSharp.small_wechatQR}
                    title="扫码加微信"
                    alt="我的微信二维码"
                    />
                </Button>
                <Popper id={id} open={open} anchorEl={anchorEl} transition>
                  {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={350}>
                      <Paper>
                        <Image fixed={data.wechatQR.childImageSharp.middle_wechatQR}  />
                      </Paper>
                    </Fade>
                  )}
                </Popper>
                <span style={{display: "block"}}>
                  微信：{ social.wechat }
                </span>
              </ContactInfo>
              
            </BioCard>
          
          )
        }}
      />
    )
  }

}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.png/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
      wechatQR: file(absolutePath: { regex: "/wechat.png/" }) {
        childImageSharp {
          small_wechatQR: fixed(width: 50, height: 50) {
            ...GatsbyImageSharpFixed
          }
          middle_wechatQR: fixed(width: 200, height: 200) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    site {
      siteMetadata {
        author
        social {
          twitter
          qq
          wechat
        }
      }
    }
  }
`

export default Bio

const Ul = styled.ul`
  margin: 0 0.3rem;
  list-style: none;
  // direction:rtl;
`
const Li = styled.li`
padding: 0.5rem 0 0.5rem 2rem;
margin: 0 0.3rem;
background: url(${pointRight}) left center no-repeat;
`

const BriefIntro = styled.div`
  // background: red;
`
const ContactInfo = styled.div`
  // background: yellow;
  margin: 0.5rem 0;
`

const WeChatLogo = styled.i`

`

const BioCard = styled.div`
box-shadow: 0px 1px 3px 2px rgba(0,0,0,0.2),
  0px 1px 1px 0px rgba(0,0,0,0.14),
  0px 2px 1px -1px rgba(0,0,0,0.12);
padding: 0 1.5rem;
// box-sizing: border-box;
border-radius: 8px;
background-color: rgba(255,255,255,0.5);
margin: 0 0 10px 0;
width: 296;
display: flex;
flex-direction: column;
align-items: center;

  @media (max-width: 450px) {
    width: 100%;
    border-radius: 0;
  }
`