import React from 'react';

import styled from "styled-components";
import { Link, graphql } from 'gatsby'


export class Header extends React.Component {

  constructor(props) {
    super(props)
    this.handleScroll = this.handleScroll.bind(this);
    this.state = {
      showPageHeader: false,
    }
  }
  _ticking = false
  _last_known_scroll_position = 0
  
  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll, { passive: true })
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll(event) {
    // do something like call `this.setState`
    // access window.scrollY etc

    // 根据位移距离大小判断是否执行操作
    // if (Math.abs(window.scrollY - this._last_known_scroll_position) < 30) {
    //   this._last_known_scroll_position = window.scrollY;
    //   return
    // }
      
    // 判断滚动方向
    if (window.scrollY > this._last_known_scroll_position) {
      // scroll down
      // 如果已经向下滚动，即state中showPageHeader为true，则不更新state,减少setState调用次数
      if (this.state.showPageHeader === true) {
        // 储存一个y轴位置，用于下次时间触发时确定相对位置，默认为0
        this._last_known_scroll_position = window.scrollY;
        return
      }
      this.setState({showPageHeader: true})
    } else {
      // scroll top
      // 如果已经向上滚动，即state中showPageHeader为false，则不必更新state,减少setState调用次数
      if (this.state.showPageHeader === false) {
        // 储存一个y轴位置，用于下次时间触发时确定相对位置，默认为0
        this._last_known_scroll_position = window.scrollY;
        return
      }
      this.setState({showPageHeader: false})
    }
    // 储存一个y轴位置，用于下次时间触发时确定相对位置，默认为0
    this._last_known_scroll_position = window.scrollY;
    
    // 由于scroll事件触发很快，有时为优化性能可能需要限制出handler中运算事件的触发
    // 参考可见https://developer.mozilla.org/en-US/docs/Web/Events/scroll
    // if (!this.ticking) {
    //   window.requestAnimationFrame(function() {
    //     doSomething(last_known_scroll_position);
    //     ticking = false;
    //   });
    //   this.ticking = true;
    // }
  }

  render() {
    return(
      <Banner>
        <AppHeader show={!this.state.showPageHeader}>
          <Link to="/">Jon's Blog</Link>
        </AppHeader>
        <PageHeader show={this.state.showPageHeader}>
          <PageHeaderWrapper>
            <PostTitle>
              {this.props.postTitle}
            </PostTitle>
          </PageHeaderWrapper>
        </PageHeader>

      </Banner>
    )
  }
}

const Banner = styled.header`
position: fixed;
top:0;
left:0;
width: 100%;
z-index: 100;
min-width: 1032px;
overflow: hidden;
background-color: rgba(255,255,255,0.9);
// box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
box-shadow: 2px 2px 2px rgba(0,0,0,.2);
background-clip: content-box;

@media (max-width: 450px) {
  width: 100%;
  min-width: 100%;
  position: absolute;
}
`

const AppHeader = styled.div`
position: relative;
display: flex;
width: 1000px;
height: 52px;
padding: 0 16px;
margin: 0 auto;
align-items: center;
transition: -webkit-transform 0.3s;
transition: transform 0.3s;
transition: transform 0.3s, -webkit-transform 0.3s;
transform: ${props => props.show ? null : "translateY(-100%)"};

@media (max-width: 450px) {
  background: #fff;
  width: 100%;
}
`

const PageHeader = styled.div`
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
transition: -webkit-transform 0.3s;
transition: transform 0.3s;
transition: transform 0.3s, -webkit-transform 0.3s;
transform: ${props => props.show ? null : "translateY(100%)"};
`

const PageHeaderWrapper = styled.div`
display: flex;
align-items: center;
// justify-content: space-between;
width: 1000px;
height: 100%;
padding: 0 16px;
margin: 0 auto;

@media (max-width: 450px) {
  background: #fff;
  width: 100%;
}
`
const PostTitle = styled.h1`
font-size: 22px;
font-weight: 600;
margin:0;
// line-height: 52px;
`