import React from 'react'
import { Link } from 'gatsby'

import { rhythm, scale } from '../utils/typography'
import styled from 'styled-components';
import tImage from '../../content/assets/bg1.jpeg'
import Bio from './Bio';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import { Header } from "./Header";
import { Footer } from "./Footer";

const styles = {
  root: {
    // flexGrow: 1,
  },
  nav1: {
    backgroundColor: "rgba(255,255,255,1  )",

  }
};


class Layout extends React.Component {

  constructor(props) {
    super(props);
  }

  _windowGlobal
  _matchMedia

  componentDidMount() {
    const _matchMedia = typeof window !== 'undefined' && window.matchMedia
  }


  render() {
    console.log("layout render method is being called.")
    const { location, title, children, classes, postTitle } = this.props
    const rootPath = `${__PATH_PREFIX__}/`
    let header

    if (location.pathname === rootPath) {
      header = (
        <h1
          style={{
            // ...scale(1.5),
            // marginBottom: rhythm(1.5),
            // marginTop: 0,
          }}
        >
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={`/`}
          >
            {title}
          </Link>
        </h1>
      )
    } else {
      header = (
        <h3
          style={{
            fontFamily: `Montserrat, sans-serif`,
            marginTop: 0,
          }}
        >
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={`/`}
          >
            {title}
          </Link>
        </h3>
      )
    }
    return (
      <LayoutContainer>     
         
          <Header postTitle={postTitle}></Header>
          <MainWrapper>
            <MainColumn>
                <PostPapper>
                  {children}
                </PostPapper>
            </MainColumn>
            <GlobalSideBar>
              <Bio />
              <Footer></Footer>
            </GlobalSideBar>
          </MainWrapper>

        {
          this._matchMedia('(max-width: 450px)').matches ? null : (
            <BgImageWrapper>
              <BgImage></BgImage>
            </BgImageWrapper>
          )
        }
      </LayoutContainer>
    )
  }
}

export default withStyles(styles)(Layout)


const BgImageWrapper = styled.ul`
list-style: none;
margin: 0;
padding: 0;
// z-index: -1;
transition: transform .3s ease-out;
`

const BgImage = styled.li`
margin:0;
padding:0;
position: fixed;
top: 0px;
left: 0;
right: 0;
bottom: 0;
opacity: 1;
z-index: -100;
background-color: transparent;
background-size: cover;
background-position: center center;
background-repeat: no-repeat;
background-image: url(${tImage});
`

